import React, { useState } from "react";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Layout from "../components/Layout";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import useStyles from "../useStyles";
import { Button } from "@material-ui/core";

function ListItemLink(props) {
  return <ListItem button component="a" {...props} />;
}

const AddProject = () => {
  const classes = useStyles();
  const [values, setValues] = useState({
    name: "",
    semester: "",
    scope: "",
    description: "",
    technologyUsed: "",
    company: "",
    courses: [],
    course: "",
    error: "",
    success: false
  });

  const {
    name,
    semester,
    scope,
    description,
    technologyUsed,
    company,
    course,
    success,
    error
  } = values;

  const handleChange = name => event => {
    setValues({ ...values, error: false, [name]: event.target.value });
  };

  const url = "http://localhost:9000";

  const addStudent = admin => {
    return fetch(`${url}/projects`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      },
      body: JSON.stringify(admin)
    })
      .then(response => {
        return response.json();
      })
      .catch(err => {
        console.log(err);
      });
  };

  const submit = event => {
    event.preventDefault();
    setValues({ ...values, error: false });

    addStudent({
      name,
      semester,
      scope,
      description,
      technologyUsed,
      company,
      course
    }).then(data => {
      if (data.error) {
        setValues({ ...values, error: data.error, success: false });
      } else {
        setValues({
          name: "",
          semester: "",
          scope: "",
          description: "",
          technologyUsed: "",
          company: "",
          course: "",
          error: "",
          success: true
        });
      }
    });
  };

  const AddProjectForm = () => {
    return (
      <React.Fragment>
        <main className={classes.layout}>
          <React.Fragment>
            <Grid>
              <main className={classes.layout}>
                <Paper className={classes.paper}>
                  <Typography component="h1" variant="h4" align="center">
                    Add New Project
                  </Typography>

                  <p></p>
                  <Grid container spacing={3}>
                    <Grid item xs={12}>
                      <TextField
                        required
                        id="name"
                        label="Project Name"
                        fullWidth
                        value={name}
                        onChange={handleChange("name")}
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <TextField
                        required
                        id="name"
                        label="Semester"
                        fullWidth
                        value={semester}
                        onChange={handleChange("semester")}
                      />
                    </Grid>

                    <Grid item xs={12}>
                      <TextField
                        rows={10}
                        required
                        id="name"
                        label="Scope"
                        fullWidth
                        value={scope}
                        onChange={handleChange("scope")}
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <TextField
                        rows={10}
                        required
                        id="name"
                        label="Description"
                        fullWidth
                        value={description}
                        onChange={handleChange("description")}
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <TextField
                        required
                        id="name"
                        label="Course Name"
                        fullWidth
                        value={technologyUsed}
                        onChange={handleChange("technologyUsed")}
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <TextField
                        required
                        id="name"
                        label="Associated Company Link"
                        fullWidth
                        value={company}
                        onChange={handleChange("company")}
                      />
                    </Grid>
                  </Grid>

                  <div className={classes.buttons}>
                    <Button
                      variant="contained"
                      color="primary"
                      className={classes.button}
                      onClick={submit}
                    >
                      Add
                    </Button>
                  </div>
                </Paper>
              </main>
            </Grid>
          </React.Fragment>
        </main>
      </React.Fragment>
    );
  };

  return (
    <Layout className="container-fluid">
      <div className="row">
        <div className="col-xl-2 col-sm-12 pb-2">
          <List component="nav" aria-labelledby="back" className={classes.root}>
            <ListItemLink href="/dashboard">
              <ArrowBackIosIcon />
              <ListItemText primary="Back to Dashboard" className="pl-2" />
            </ListItemLink>
          </List>
        </div>
        <div className="col-xl-10 col-sm-12 p-2">{AddProjectForm()}</div>
      </div>
    </Layout>
  );
};

export default AddProject;
