/* eslint-disable no-lone-blocks */
import React, { useMemo, useState, useEffect } from "react";
import {
  Paper,
  Button,
  TextField,
  Grid,
  Table,
  TableRow,
  TableBody,
  TableHead,
  TableCell,
  Toolbar,
  Dialog,
  Slide,
  Typography,
  IconButton
} from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";

import useStyles from "../useStyles";
import axios from "axios";
import { useTable, useFilters, useSortBy, usePagination } from "react-table";

const url = "http://localhost:9000/students";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function Students() {
  const classes = useStyles();
  const [data, setData] = useState([]);
  const [studentID, setStudentID] = useState([""]);
  const [objectID, setObjectID] = useState([""]);
  const [studentName, setStudentName] = useState("");
  const [studentYear, setStudentYear] = useState("");

  const [open, setOpen] = React.useState(false);

  function updateItem(id) {
    fetch(url + "/" + id, {
      method: "put",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json"
      },
      body: JSON.stringify({
        studentID: studentID,
        studentName: studentName,
        studentYear: studentYear
      }) //change param here
    })
      .then(res => res.json())
      .then(() => window.location.reload())
      .catch(() => alert("There was an error, please try again"));
  }

  function deleteItem(id) {
    fetch(url + "/" + id, {
      method: "delete"
    })
      .then(res => res.json())
      .then(() => window.location.reload())
      .catch(() => alert("There was an error, please try again"));
  }

  const handleClose = () => {
    setOpen(false);
  };
  const handleClickOpen = () => {
    setOpen(true);
  };

  function editItem(studentName, studentId, studentYear, objectID) {
    setStudentName(studentName);
    setStudentID(studentId);
    setStudentYear(studentYear);
    setObjectID(objectID);
  }

  // Using useEffect to call the API once mounted and set the data

  useEffect(() => {
    (async () => {
      const result = await axios(url);
      var list = result.data.filter(
        products =>
          products.studentID && products.studentName && products.studentYear
      );
      setData(list);
    })();
  }, []);

  function StudentTable({ columns, data }) {
    const [filterInput, setFilterInput] = useState("");

    // Use the state and functions returned from useTable to build your UI
    const {
      getTableProps,
      getTableBodyProps,
      headerGroups,
      prepareRow,
      setFilter,
      pageOptions,
      page,
      state: { pageIndex, pageSize },
      previousPage,
      nextPage,
      setPageSize,
      canPreviousPage,
      canNextPage
    } = useTable(
      {
        columns,
        data
      },
      useFilters,
      useSortBy,
      usePagination
    );

    const handleFilterChange = e => {
      const value = e.target.value || undefined;
      setFilter("studentName", value);
      setFilterInput(value);
    };

    return (
      <>
        <div className={classes.root}>
          <Paper className={classes.paper}>
            <Grid container>
              <Grid item xs={4}>
                <Typography
                  className={classes.title}
                  variant="h5"
                  id="tableTitle"
                >
                  Students
                </Typography>
              </Grid>
              <Grid item xs={8} item align="right">
                <TextField
                  classes={classes.margin}
                  id="standard-basic"
                  label="Search"
                  size="small"
                  variant="standard"
                  onChange={handleFilterChange}
                />
              </Grid>
            </Grid>

            <Grid>
              <Table {...getTableProps()}>
                <TableHead>
                  {headerGroups.map(headerGroup => (
                    <TableRow {...headerGroup.getHeaderGroupProps()}>
                      {headerGroup.headers.map(column => (
                        <TableCell
                          {...column.getHeaderProps(
                            column.getSortByToggleProps()
                          )}
                          className={
                            column.isSorted
                              ? column.isSortedDesc
                                ? "sort-desc"
                                : "sort-asc"
                              : ""
                          }
                        >
                          {column.render("Header")}
                        </TableCell>
                      ))}
                    </TableRow>
                  ))}
                </TableHead>
                <TableBody {...getTableBodyProps()}>
                  {page.map((row, i) => {
                    prepareRow(row);
                    return (
                      <TableRow {...row.getRowProps()}>
                        {row.cells.map(cell => {
                          return (
                            <TableCell
                              {...cell.getCellProps()}
                              studentId={studentID}
                            >
                              {cell.render("Cell")}
                            </TableCell>
                          );
                        })}
                      </TableRow>
                    );
                  })}
                </TableBody>
              </Table>
              <br />
              <div>
                <Grid align="center">
                  <Button
                    color="primary"
                    onClick={() => previousPage()}
                    disabled={!canPreviousPage}
                  >
                    <ChevronLeftIcon />
                  </Button>
                  Page{" "}
                  <em>
                    {pageIndex + 1} of {pageOptions.length}
                  </em>
                  <Button
                    color="primary"
                    onClick={() => nextPage()}
                    disabled={!canNextPage}
                  >
                    <ChevronRightIcon />
                  </Button>
                  <select
                    value={pageSize}
                    onChange={e => {
                      setPageSize(Number(e.target.value));
                    }}
                  >
                    {[5, 10, 20].map(pageSize => (
                      <option key={pageSize} value={pageSize}>
                        {pageSize}
                      </option>
                    ))}
                  </select>
                </Grid>
              </div>
            </Grid>
          </Paper>
        </div>
      </>
    );
  }

  const columns = useMemo(
    () => [
      {
        Header: (
          <div style={{ textAlign: "left", fontWeight: "bold" }}>
            Student Name
          </div>
        ),
        id: "studentName",
        accessor: "studentName"
      },

      {
        Header: (
          <div style={{ textAlign: "center", fontWeight: "bold" }}>
            Student ID
          </div>
        ),
        id: "studentID",
        accessor: "studentID",
        Cell: ({ row }) => (
          <div style={{ textAlign: "center" }}>{row.original.studentID}</div>
        )
      },
      {
        Header: (
          <div style={{ textAlign: "right", fontWeight: "bold" }}>Year</div>
        ),
        id: "studentYear",
        accessor: "studentYear",
        Cell: ({ row }) => (
          <div style={{ textAlign: "right" }}>{row.original.studentYear}</div>
        )
      },

      {
        Header: () => (
          <div style={{ textAlign: "center", fontWeight: "bold" }}>Action</div>
        ),
        id: "action",
        Cell: ({ row }) => (
          <div style={{ textAlign: "center" }}>
            <Button
              color="primary"
              onClick={e => {
                handleClickOpen(e);
                editItem(
                  row.original.studentName,
                  row.original.studentID,
                  row.original.studentYear,
                  row.original._id
                );
              }}
            >
              Edit{" "}
            </Button>
            <Button
              color="secondary"
              onClick={() => {
                {
                  window.confirm(
                    "Delete this student? \n" + row.original.studentName
                  )
                    ? deleteItem(row.original._id)
                    : alert("Action cancelled.");
                }
              }}
              aria-label="delete"
            >
              Delete{" "}
            </Button>
          </div>
        )
      }
    ],
    []
  );

  return (
    <div>
      <div>
        <StudentTable columns={columns} data={data} />

        <Dialog
          fullScreen
          open={open}
          onClose={handleClose}
          TransitionComponent={Transition}
        >
          <Toolbar>
            <IconButton
              edge="start"
              color="inherit"
              onClick={handleClose}
              aria-label="close"
            >
              <CloseIcon />
            </IconButton>
          </Toolbar>
          <React.Fragment>
            <Grid>
              <main className={classes.layout}>
                <Paper className={classes.paper}>
                  <Typography component="h1" variant="h4" align="center">
                    Edit Student
                  </Typography>

                  <p></p>
                  <Grid container spacing={3}>
                    <Grid item xs={12} sm={12} lg={12}>
                      <TextField
                        onInvalid
                        required
                        id="studentName"
                        name="studentName"
                        label="Name"
                        fullWidth
                        autoComplete="studentName"
                        value={studentName}
                        onChange={e => setStudentName(e.target.value)}
                      />
                    </Grid>
                    <Grid item xs={12} sm={7} lg={7}>
                      <TextField
                        required
                        id="studentId"
                        name="studentId"
                        label="Student ID"
                        fullWidth
                        autoComplete="studentID"
                        value={studentID}
                        onChange={e => setStudentID(e.target.value)}
                      />
                    </Grid>
                    <Grid item xs={12} sm={5} lg={5}>
                      <TextField
                        required
                        id="studentYear"
                        name="studentYear"
                        label="Year"
                        type="number"
                        fullWidth
                        autoComplete="studentYear"
                        value={studentYear}
                        inputProps={{ min: 1, max: 5 }}
                        onChange={e => setStudentYear(e.target.value)}
                      />
                    </Grid>
                    <Grid align="right" item xs={12}>
                      <Button
                        autoFocus
                        color="primary"
                        onClick={() => {
                          handleClose();
                          updateItem(objectID);
                        }}
                      >
                        Save changes
                      </Button>
                    </Grid>
                  </Grid>
                </Paper>
              </main>
            </Grid>
          </React.Fragment>
        </Dialog>
      </div>
    </div>
  );
}
