/* eslint-disable no-lone-blocks */
import React, { useMemo, useState, useEffect } from "react";
import {
  Paper,
  Button,
  TextField,
  Grid,
  Table,
  TableRow,
  TableBody,
  TableHead,
  TableCell,
  Toolbar,
  Dialog,
  Slide,
  Typography,
  IconButton
} from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";

import useStyles from "../useStyles";
import axios from "axios";
import { useTable, useFilters, useSortBy, usePagination } from "react-table";

const url = "http://localhost:9000/projects";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function Courses() {
  const classes = useStyles();
  const [data, setData] = useState([]);
  const [objectID, setObjectID] = useState([""]);
  const [name, setName] = useState([""]);
  const [semester, setSemester] = useState("");
  const [scope, setScope] = useState("");
  const [description, setDescription] = useState("");
  const [technologyUsed, setTechnology] = useState("");
  const [company, setCompany] = useState("");
  const [course, setCourse] = useState("");

  const [open, setOpen] = React.useState(false);

  function updateItem(id) {
    fetch(url + "/" + id, {
      method: "put",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json"
      },
      body: JSON.stringify({
        name: name,
        semester: semester,
        scope: scope,
        description: description,
        technologyUsed: technologyUsed,
        company: company,
        course: course
      }) //change param here
    })
      .then(res => res.json())
      .then(() => window.location.reload())
      .catch(() => alert("There was an error, please try again"));
  }

  function deleteItem(id) {
    fetch(url + "/" + id, {
      method: "delete"
    })
      .then(res => res.json())
      .then(() => window.location.reload())
      .catch(() => alert("There was an error, please try again"));
  }

  const handleClose = () => {
    setOpen(false);
  };
  const handleClickOpen = () => {
    setOpen(true);
  };

  function editItem(
    objectID,
    name,
    semester,
    scope,
    description,
    technologyUsed,
    company,
    course
  ) {
    setObjectID(objectID);
    setName(name);
    setSemester(semester);
    setScope(scope);
    setDescription(description);
    setTechnology(technologyUsed);
    setCompany(company);
    setCourse(course);
  }

  // Using useEffect to call the API once mounted and set the data

  useEffect(() => {
    (async () => {
      const result = await axios(url);
      var list = result.data.filter(
        projects =>
          projects.name &&
          projects.semester &&
          projects.scope &&
          projects.description &&
          projects.technologyUsed &&
          projects.company &&
          projects.course
      );
      setData(list);
    })();
  }, []);

  function CourseTable({ columns, data }) {
    const [filterInput, setFilterInput] = useState("");

    // Use the state and functions returned from useTable to build your UI
    const {
      getTableProps,
      getTableBodyProps,
      headerGroups,
      prepareRow,
      setFilter,
      pageOptions,
      page,
      state: { pageIndex, pageSize },
      previousPage,
      nextPage,
      setPageSize,
      canPreviousPage,
      canNextPage
    } = useTable(
      {
        columns,
        data
      },
      useFilters,
      useSortBy,
      usePagination
    );

    const handleFilterChange = e => {
      const value = e.target.value || undefined;
      setFilter("name", value);
      setFilterInput(value);
    };

    return (
      <>
        <div className={classes.root}>
          <Paper className={classes.paper}>
            <Grid container>
              <Grid item xs={4}>
                <Typography
                  className={classes.title}
                  variant="h5"
                  id="tableTitle"
                >
                  Projects
                </Typography>
              </Grid>
              <Grid item xs={8} item align="right">
                <TextField
                  classes={classes.margin}
                  id="standard-basic"
                  label="Search"
                  size="small"
                  variant="standard"
                  onChange={handleFilterChange}
                />
              </Grid>
            </Grid>

            <Grid>
              <Table {...getTableProps()}>
                <TableHead>
                  {headerGroups.map(headerGroup => (
                    <TableRow {...headerGroup.getHeaderGroupProps()}>
                      {headerGroup.headers.map(column => (
                        <TableCell
                          {...column.getHeaderProps(
                            column.getSortByToggleProps()
                          )}
                          className={
                            column.isSorted
                              ? column.isSortedDesc
                                ? "sort-desc"
                                : "sort-asc"
                              : ""
                          }
                        >
                          {column.render("Header")}
                        </TableCell>
                      ))}
                    </TableRow>
                  ))}
                </TableHead>
                <TableBody {...getTableBodyProps()}>
                  {page.map((row, i) => {
                    prepareRow(row);
                    return (
                      <TableRow {...row.getRowProps()}>
                        {row.cells.map(cell => {
                          return (
                            <TableCell {...cell.getCellProps()} name={name}>
                              {cell.render("Cell")}
                            </TableCell>
                          );
                        })}
                      </TableRow>
                    );
                  })}
                </TableBody>
              </Table>
              <br />
              <div>
                <Grid align="center">
                  <Button
                    color="primary"
                    onClick={() => previousPage()}
                    disabled={!canPreviousPage}
                  >
                    <ChevronLeftIcon />
                  </Button>
                  Page{" "}
                  <em>
                    {pageIndex + 1} of {pageOptions.length}
                  </em>
                  <Button
                    color="primary"
                    onClick={() => nextPage()}
                    disabled={!canNextPage}
                  >
                    <ChevronRightIcon />
                  </Button>
                  <select
                    value={pageSize}
                    onChange={e => {
                      setPageSize(Number(e.target.value));
                    }}
                  >
                    {[5, 10, 20].map(pageSize => (
                      <option key={pageSize} value={pageSize}>
                        {pageSize}
                      </option>
                    ))}
                  </select>
                </Grid>
              </div>
            </Grid>
          </Paper>
        </div>
      </>
    );
  }

  const columns = useMemo(
    () => [
      {
        Header: (
          <div style={{ textAlign: "left", fontWeight: "bold" }}>
            Project Name
          </div>
        ),
        id: "courseName",
        accessor: "courseName",
        Cell: ({ row }) => (
          <div style={{ textAlign: "left" }}>{row.original.name}</div>
        )
      },

      {
        Header: (
          <div style={{ textAlign: "center", fontWeight: "bold" }}>
            Semester
          </div>
        ),
        id: "name",
        accessor: "name",
        Cell: ({ row }) => (
          <div style={{ textAlign: "center" }}>{row.original.semester}</div>
        )
      },

      {
        Header: () => (
          <div style={{ textAlign: "center", fontWeight: "bold" }}>Action</div>
        ),
        id: "action",
        Cell: ({ row }) => (
          <div style={{ textAlign: "center" }}>
            <Button
              color="primary"
              onClick={e => {
                handleClickOpen(e);
                editItem(
                  row.original._id,
                  row.original.name,
                  row.original.semester,
                  row.original.scope,
                  row.original.description,
                  row.original.technologyUsed,
                  row.original.company,
                  row.original.course
                );
              }}
            >
              Edit{" "}
            </Button>
            <Button
              color="secondary"
              onClick={() => {
                {
                  window.confirm("Delete this project?")
                    ? deleteItem(row.original._id)
                    : alert("Action cancelled.");
                }
              }}
              aria-label="delete"
            >
              Delete{" "}
            </Button>
          </div>
        )
      }
    ],
    []
  );

  return (
    <div>
      <div>
        <CourseTable columns={columns} data={data} />

        <Dialog
          fullScreen
          open={open}
          onClose={handleClose}
          TransitionComponent={Transition}
        >
          <Toolbar>
            <IconButton
              edge="start"
              color="inherit"
              onClick={handleClose}
              aria-label="close"
            >
              <CloseIcon />
            </IconButton>
          </Toolbar>
          <React.Fragment>
            <Grid>
              <main className={classes.layout}>
                <Paper className={classes.paper}>
                  <Typography component="h1" variant="h4" align="center">
                    Edit Project
                  </Typography>

                  <p></p>
                  <Grid container spacing={3}>
                    <Grid item xs={12} sm={12} lg={12}>
                      <TextField
                        onInvalid
                        required
                        id="courseName"
                        name="courseName"
                        label="Project Name"
                        fullWidth
                        autoComplete="courseName"
                        value={name}
                        onChange={e => setName(e.target.value)}
                      />
                    </Grid>
                    <Grid item xs={12} sm={12} lg={12}>
                      <TextField
                        required
                        id="name"
                        name="name"
                        label="Semester"
                        fullWidth
                        autoComplete="name"
                        value={semester}
                        onChange={e => setSemester(e.target.value)}
                      />
                    </Grid>
                    <Grid item xs={12} sm={12} lg={12}>
                      <TextField
                        required
                        id="name"
                        name="name"
                        label="Scope"
                        fullWidth
                        autoComplete="name"
                        value={scope}
                        onChange={e => setScope(e.target.value)}
                      />
                    </Grid>
                    <Grid item xs={12} sm={12} lg={12}>
                      <TextField
                        required
                        id="name"
                        name="name"
                        label="Description"
                        fullWidth
                        autoComplete="name"
                        value={description}
                        onChange={e => setDescription(e.target.value)}
                      />
                    </Grid>
                    <Grid item xs={12} sm={12} lg={12}>
                      <TextField
                        required
                        id="name"
                        name="name"
                        label="Company Link"
                        fullWidth
                        autoComplete="name"
                        value={company}
                        onChange={e => setCompany(e.target.value)}
                      />
                    </Grid>
                    <Grid item xs={12} sm={12} lg={12}>
                      <TextField
                        required
                        id="name"
                        name="name"
                        label="Technology Used"
                        fullWidth
                        autoComplete="name"
                        value={technologyUsed}
                        onChange={e => setTechnology(e.target.value)}
                      />
                    </Grid>

                    <Grid align="right" item xs={12}>
                      <Button
                        autoFocus
                        color="primary"
                        onClick={() => {
                          handleClose();
                          updateItem(objectID);
                        }}
                      >
                        Save changes
                      </Button>
                    </Grid>
                  </Grid>
                </Paper>
              </main>
            </Grid>
          </React.Fragment>
        </Dialog>
      </div>
    </div>
  );
}
