import React, { useState } from "react";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Layout from "../components/Layout";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import useStyles from "../useStyles";
import { Button } from "@material-ui/core";

function ListItemLink(props) {
  return <ListItem button component="a" {...props} />;
}

const AddStudent = () => {
  const classes = useStyles();
  const [values, setValues] = useState({
    studentID: "",
    studentName: "",
    studentYear: "",
    error: "",
    success: false
  });

  const { studentID, studentName, studentYear, success, error } = values;

  const handleChange = name => event => {
    setValues({ ...values, error: false, [name]: event.target.value });
  };

  const url = "http://localhost:9000";

  const addStudent = admin => {
    return fetch(`${url}/students`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      },
      body: JSON.stringify(admin)
    })
      .then(response => {
        return response.json();
      })
      .catch(err => {
        console.log(err);
      });
  };

  const submit = event => {
    event.preventDefault();
    setValues({ ...values, error: false });

    addStudent({ studentID, studentName, studentYear }).then(data => {
      if (data.error) {
        setValues({ ...values, error: data.error, success: false });
      } else {
        setValues({
          ...values,
          studentID: "",
          studentName: "",
          studentYear: "",
          error: "",
          success: true
        });
      }
    });
  };

  const showError = () => (
    <div
      className="alert alert-danger text-center"
      style={{ display: error ? "" : "none" }}
    >
      {error}
    </div>
  );

  const showSuccess = () => (
    <div
      className="alert alert-success text-center"
      style={{ display: success ? "" : "none" }}
    >
      Student succesfully created
    </div>
  );

  const AddStudentForm = () => {
    return (
      <React.Fragment>
        <main className={classes.layout}>
          <React.Fragment>
            <Grid>
              <main className={classes.layout}>
                <Paper className={classes.paper}>
                  <Typography component="h1" variant="h4" align="center">
                    Add New Student
                  </Typography>

                  <p></p>
                  <Grid container spacing={3}>
                    <Grid item xs={12}>
                      <TextField
                        required
                        id="id"
                        name="studentId"
                        label="Student ID"
                        fullWidth
                        value={studentID}
                        onChange={handleChange("studentID")}
                      />
                    </Grid>
                    <Grid item xs={8}>
                      <TextField
                        required
                        id="name"
                        name="studentName"
                        label="Student Name"
                        fullWidth
                        value={studentName}
                        onChange={handleChange("studentName")}
                      />
                    </Grid>
                    <Grid item xs={4}>
                      <TextField
                        id="year"
                        name="studentYear"
                        label="Student Year"
                        type="number"
                        fullWidth
                        value={studentYear}
                        onChange={handleChange("studentYear")}
                      />
                    </Grid>
                  </Grid>

                  <div className={classes.buttons}>
                    <Button
                      variant="contained"
                      color="primary"
                      className={classes.button}
                      onClick={submit}
                    >
                      Add
                    </Button>
                  </div>
                </Paper>
              </main>
            </Grid>
          </React.Fragment>
        </main>
      </React.Fragment>
    );
  };

  return (
    <Layout className="container-fluid">
      <div className="row">
        <div className="col-xl-2 col-sm-12 pb-2">
          <List component="nav" aria-labelledby="back" className={classes.root}>
            <ListItemLink href="/students">
              <ArrowBackIosIcon />
              <ListItemText primary="Back to Students" className="pl-2" />
            </ListItemLink>
          </List>
        </div>
        <div className="col-xl-10 col-sm-12 p-2">
          {showError()}
          {showSuccess()}
          {AddStudentForm()}
        </div>
      </div>
    </Layout>
  );
};

export default AddStudent;
