import React from "react";
import "./App.css";
import { BrowserRouter, Route } from "react-router-dom";
import AdminDashboard from "./pages/AdminDashboard";
import NavBar from "./components/NavBar";
import AddProjectForm from "./pages/AddProject";
import Footer from "./components/Footer";
import Student from "./pages/Student";
import Course from "./pages/Course";
import LoginPage from "./pages/LogIn";
import AddCourse from "./pages/AddCourse";
import AddStudent from "./pages/AddStudent";
import AddAdmin from "./pages/AddAdmin";
import AdminRoute from "./auth/AdminRoute";
import Home from "./pages/Home";
import ProjectPage from "./pages/ProjectPage";

function App() {
  return (
    <BrowserRouter>
      <div>
        <NavBar />
        <Route path="/" component={Home} exact />
        <AdminRoute path="/dashboard" component={AdminDashboard} exact />
        <AdminRoute path="/projects/add" component={AddProjectForm} />
        <AdminRoute path="/students" component={Student} exact />
        <AdminRoute path="/courses" component={Course} exact />
        <AdminRoute path="/courses/add" component={AddCourse} />
        <AdminRoute path="/students/add" component={AddStudent} />
        <Route path="/login" component={LoginPage} />
        <AdminRoute path="/admins/add" component={AddAdmin} />
        <Route path="/projects/:id" component={ProjectPage} />
        <Footer />
      </div>
    </BrowserRouter>
  );
}

export default App;
