import React, { useState, useEffect, useMatchParams } from "react";
import { useParams, Link } from "react-router-dom";
import useStyles from "../useStyles";
import {
  Grid,
  Paper,
  Typography,
  Button,
  Table,
  TableCell,
  TableRow,
  Divider
} from "@material-ui/core";
import Layout from "../components/Layout";

const url = "http://localhost:9000/projects/";

export default function ProjectPage(props) {
  const classes = useStyles();
  const [projects, setProjects] = useState([]);
  const [company, setCompany] = useState("");
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [technologyUsed, setTechnologyUsed] = useState("");
  const [scope, setScope] = useState("");

  const [isLoading, setIsLoading] = useState(true);

  const { id } = props.match.params.id;

  useEffect(() => {
    fetch(url + id, {
      method: "get",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json"
      }
    })
      .then(res => res.json())
      .then(response => {
        setProjects(response);
        setIsLoading(false);
      })
      .catch(() => alert("There was an error, please try again"));
  }, []);

  return (
    <div>
      <Layout title="Details" className="container-fluid">
        <React.Fragment>
          <Grid>
            <main className={classes.layout}>
              <Paper className={classes.paper}>
                <Grid align="left">
                  <Link to="/projects">
                    <Button color="primary" className={classes.button}>
                      Back
                    </Button>
                  </Link>
                </Grid>
                <p></p>
                <Table>
                  <TableRow hover tabIndex={-1}>
                    <Typography align="left" variant="h6" gutterBottom>
                      {projects.name}
                    </Typography>
                  </TableRow>
                  <TableRow hover tabIndex={-1}>
                    <Typography align="left" variant="subtitle1">
                      <Divider />
                      Link to Industry: {projects.company}
                    </Typography>
                  </TableRow>
                  <Divider />
                  <TableRow hover tabIndex={-1}>
                    <Typography align="left" variant="subtitle1">
                      <Divider />
                      Technology used: {projects.technologyUsed}
                    </Typography>
                  </TableRow>
                  <TableRow hover tabIndex={-1}>
                    <Typography align="left" variant="subtitle1">
                      <Divider />
                      Scope: {projects.scope}
                    </Typography>
                  </TableRow>
                  <Divider />
                  <TableRow hover tabIndex={-1}>
                    <Typography align="left" variant="subtitle1">
                      Description: {projects.description}
                    </Typography>
                  </TableRow>
                </Table>
              </Paper>
            </main>
          </Grid>
        </React.Fragment>
      </Layout>
    </div>
  );
}
