import React from "react";
import "./style.css";
import logo from "../img/rmit-logo.png";
import { Button } from "@material-ui/core";
import { isAuthenticated, signOut } from "../auth";
import { useHistory, Link } from "react-router-dom";

const Navbar = () => {
  const isActive = (history, path) => {
    if (history.location.pathname === path) {
      return { color: "#909090" };
    } else {
      return { color: "#ffffff" };
    }
  };
  const history = useHistory();
  return (
    <nav className="navbar relative navbar-custom navbar-expand-lg navbar-template">
      <a className="navbar-brand font-weight-bold" href="/">
        <img className="logo pr-2" src={logo} alt="" />
        RMIT
      </a>

      {!isAuthenticated() && (
        <div className="ml-auto">
          <Link
            className="nav-link"
            style={isActive(history, "/login")}
            to="/login"
          >
            <Button color="primary" className="text-white">
              Sign In
            </Button>
          </Link>
        </div>
      )}

      {isAuthenticated() && (
        <div className="">
          <Link
            className="nav-link"
            style={{ cursor: "pointer", color: "#ffffff" }}
            to="/dashboard"
          >
            <Button color="primary" className="text-white">
              Dashboard
            </Button>
          </Link>
        </div>
      )}

      {isAuthenticated() && (
        <div className="ml-auto">
          <Link
            className="nav-link"
            style={{ cursor: "pointer", color: "#ffffff" }}
            to="/"
            onClick={() => signOut()}
          >
            <Button color="primary" className="text-white">
              Sign Out
            </Button>
          </Link>
        </div>
      )}
    </nav>
  );
};

export default Navbar;
